html,
body {
    width: 100%;
    height: 100%;
    font-size: 100%;
    font-family: "Raleway", sans-serif;
    position: relative;
}

@media only screen and (max-width: 600px) {
    /*Header*/
    .container .about-me-heading {
        margin-top: 5%;
        text-align: center;
    }
    .container .about-me-content {
        margin-top: 5%;
    }
    /*Footer*/
    .social-media-links img {
        width: 8%;
        margin: 5%;
    }
    .container-fluid .footer {
        position: relative;
    }
    .about-me-progress {
        margin-top: 5%;
    }
    .latest-work img {
        width: 100%;
    }
    .latest-work img:hover {
        width: 100% !important;
    }
    .about-me-progress .skills {
        font-size: 130%;
        font-weight: bold;
        text-align: center;
        display: block;
    }
    .header .full-height {
        height: 65vh;
    }
    .header .full-height {
        font-size: 115%;
    }
    .latest-work .link {
        display: none;
    }
}


/*START: Header*/

.header {
    width: 100%;
    background-color: #f8f8ff;
    text-align: center;
    font-size: 125%;
    overflow: hidden;
}

.my-name {
    font-family: "Anton", sans-serif;
    font-size: 280%;
}


/*END: Header*/


/*STARTER: Body*/

.content {
    text-align: justify;
}

.content a {
    text-decoration: none;
    color: #000000;
}

.content a:hover {
    text-decoration: none;
    color: #ffffff !important;
}


/* Style that first letter! */

.content:first-letter {
    font-size: 250%;
    font-weight: bold;
    line-height: normal;
}

.full-height {
    height: 50vh;
}

button:hover {
    color: #ffffff !important;
    background-color: #1c2331;
}

.heading {
    font-size: 175%;
    font-weight: bold;
    text-align: center;
    margin-top: 2%;
}

.sub-heading {
    font-size: 120%;
    text-align: center;
    margin-top: 1%;
}

.about-me-content {
    margin-top: 2%;
    text-align: center;
}

.about-me-progress {
    text-align: justify;
}

.latest-work img:hover {
    width: 100%;
    cursor: pointer;
}

.skills {
    display: none;
}


/*END: Body*/


/*START: Footer*/

.footer {
    background-color: #1c2331;
    text-align: center;
    padding-bottom: 1%;
    font-size: 90%;
    color: #ffffff;
    width: 100%;
    position: relative;
    bottom: 0;
    left: 0;
    right: 0;
}

.name {
    font-size: 320%;
    font-family: "Engagement", cursive;
}

hr {
    background-color: #ffffff;
    width: 80%;
    opacity: 0.5;
}


/*END: Footer*/